@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500&display=swap);
.Popup {
  display: flex;
  justify-content: center;
  align-self: center;
  padding-top: 55vh;
}

.NavMain {
  background-color: #8ecff4;
  color: rgb(57, 57, 57);
  display: grid;
  grid-template-columns: 33% 33% 33%;
  align-items: center;
  padding: 15px;
  height: 80px;
  width: 100%;
  position: fixed;
  margin: 0;
  left: 0;
  bottom: 0;
}

.NavMain * {
  display: flex;
  justify-content: center;
  align-items: center;
}

.NavMain .fa-sign-in-alt {
  width: 35px;
  height: auto;
}

.NavMain .fa-clipboard-list {
  width: 25px;
  height: auto;
}

.NavMain .fa-home {
  width: 40px;
  height: auto;
}

.NavMain .profileImage {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 3px solid blue;
}

.NavMain.about-page {
  background-color: #ecb002;
}

.NavMain a {
  color: aliceblue;
}

.NavMain .MuiAvatar-circle {
  height: 50px;
  width: 50px;
  box-shadow: rgba(0, 0, 0, 0.35) 1.95px 1.95px 2.6px;
}

.background-form {
  background-color: #aeeffd;
  /* background-size: cover; */
  padding-top: 65px;
  height: 800px;
}

/* FOR ITEM FORM  */

.background-item-form {
  background-color: #aeeffd;
  /* background-size: cover; */
  /* padding: 65px 0; */
  /* height: 800px; */
  padding: 100px 10px 100px 10px;
  /* margin: 5% auto; */
}

.signin {
  padding-top: 350px;
}

.edit {
  padding-top: 100px;
}

.form-container {
  background-color: #ededed;
  border-radius: 1rem;
  /* padding: 5% auto; */
  margin: 10% auto;
  width: 85%;
  padding: 10px;
}

/* FOR ITEM FORM  */

.item-form-container {
  /* padding-bottom: 100px; */
  background-color: #ededed;
  border-radius: 1rem;
  /* padding: 5% auto; */
  margin: auto;
  width: 85%;
  padding: 10px;
}

.Form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* padding-bottom: 100px; */
  /* margin: 40px 5px 0 5px; */
  bottom: 0;
  height: 100%;
  margin-bottom: auto;
  /* overflow: scroll; */
}

.Form .title {
  margin-bottom: 20px;
  font-size: 2.25rem;
}

.Form .profile-photo {
  border: 2px solid dodgerblue;
  height: 150px;
}

.Form input {
  padding: 10px 12px;
  font-size: 1rem;
  background-color: whitesmoke;
  border: 1px solid black;
  margin-bottom: 10px;
  border-radius: 2em;
  width: 90%;
  box-shadow: rgba(0, 0, 0, 0.35) 1.95px 1.95px 2.6px;
  /* align-items: center; */
}

.Form .link {
  color: dodgerblue;
}

.Form .link:hover {
  color: purple;
}

.Form .question {
  margin-top: 10px;
}

.Form label {
  margin-bottom: 5px;
}

.Form button {
  max-width: 200px;
  padding: 10px 12px;
  margin: 10px 4px;
  font-size: 1.25rem;
  background-color: dodgerblue;
  color: white;
  border: 1px solid black;
  cursor: pointer;
  transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
  border-radius: 2em;
  box-shadow: rgba(0, 0, 0, 0.35) 1.95px 1.95px 2.6px;
}

.Form #delete {
  max-width: 200px;
  padding: 10px 12px;
  margin: 10px 4px;
  font-size: 1.25rem;
  background-color: rgb(221, 76, 76);
  color: white;
  border: 1px solid black;
  cursor: pointer;
  transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
  border-radius: 2em;
  box-shadow: rgba(0, 0, 0, 0.35) 1.95px 1.95px 2.6px;
}

.Form #logout {
  max-width: 200px;
  padding: 10px 12px;
  margin: 10px 4px;
  font-size: 1.25rem;
  background-color: dodgerblue;
  color: white;
  border: 1px solid black;
  cursor: pointer;
  transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
  border-radius: 2em;
  box-shadow: rgba(0, 0, 0, 0.35) 1.95px 1.95px 2.6px;
}

.Form button:hover {
  background-color: #12fa85;
  color: black;
}

.form-group {
  text-align: center;
}

.price-container .price {
  width: 50px;
  text-align: center;
  /* padding: 0; */
  margin: 0;
}

.price-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 5px;
}

.price-over-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.ratings-over-container {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.ratings-over-container div {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  /* align-items: center; */
  width: 100%;
  margin-top: 10px;
}

.input {
  /* margin: 8px 0 12px; */
  width: 100%;
  border-radius: 4px;
  border: none;
  font-size: 14px;
  background: #eff1f6;
  box-shadow: rgba(0, 0, 0, 0.35) 1.95px 1.95px 2.6px;
  /* padding: 12px 12px; */
}

.Form select {
  padding: 5px;
  border-radius: 2em;
  box-shadow: rgba(0, 0, 0, 0.35) 1.95px 1.95px 2.6px;
}
textarea {
  display: block;
  font-size: 14px;
  width: 100%;
  height: 100px;
  background: #eff1f6;
  border: none;
  border-radius: 4px;
  line-height: 1.3;
  resize: none;
  box-sizing: border-box;
  /* padding: 12px; */
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: "Manrope", sans-serif;
  border: 1px solid black;
  box-shadow: rgba(0, 0, 0, 0.35) 1.95px 1.95px 2.6px;
}

.form-group .label {
  font-weight: 600;
}

.LogList {
  background-color: whitesmoke;
  height: 100%;
  padding: 15px 5px;
}

.LogElementCard {
  width: 100%;
  margin: 0;
  padding: 0;
}

.LogList .ui.card {
  margin: 10px auto;
  width: 100%;
  background-color: rgb(240, 239, 239);
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.LogList .ui.card:last-child {
  padding-bottom: 80px;
}

.LogElement {
  height: 120px;
  width: 100%;
  display: flex;
  flex-direction: row;
  background-color: rgb(224, 239, 250);
  border: 1px solid grey;
  justify-content: space-between;
  align-items: center;
  border-radius: 2em;
  margin: 5px 0;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.LogElement button {
  max-width: 200px;
  padding: 10px 10px;
  height: 35px;
  width: 35px;
  margin: 10px;
  font-size: 1.25rem;
  background-color: dodgerblue;
  color: white;
  border: 1px solid black;
  cursor: pointer;
  transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
  border-radius: 2em;
  display: flex;
  justify-content: center;
  box-shadow: rgba(0, 0, 0, 0.35) 1.95px 1.95px 2.6px;
}

.LogElement button:hover {
  background-color: #12fa85;
  color: black;
}

.LogElement #delete {
  background-color: red;
  color: aliceblue;
  max-width: 200px;
  padding: 10px 10px;
  height: 35px;
  width: 35px;
  margin: 10px;
  font-size: 1.25rem;
  color: white;
  border: 1px solid black;
  cursor: pointer;
  transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
  border-radius: 2em;
  display: flex;
  justify-content: center;
  box-shadow: rgba(0, 0, 0, 0.35) 1.95px 1.95px 2.6px;
}

.LogElement #edit {
  background-color: green;
  color: aliceblue;
}

.LogElement .beer-info {
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-left: 20px;
  /* border-radius: 2em;
  box-shadow: rgba(0, 0, 0, 0.35) 1.95px 1.95px 2.6px;
  background-color: dodgerblue; */
}

.LogElement .beer-info * {
  padding: 0;
  margin: 0;
}

.LogElement .options {
  display: flex;
}

.LogElement .fa-edit,
.LogElement .fa-trash-alt {
  height: 15px;
  width: 15px;
}

.AutocompletePlace {
  position: relative;
}

.AutocompletePlace-results {
  position: absolute;
  background-color: white;
  padding: 0;
  margin: 0;
  border: 1px solid #00000011;
}

.AutocompletePlace-items {
  list-style: none;
  border: 1px solid #00000011;
  cursor: pointer;
  padding: 10px 10px;
}

.AutocompletePlace-items:hover {
  background-color: #0000ff11;
}

.About {
  background-image: url("/.././beer_background.jpg");
